import Vue from 'vue';
import iView from 'iview';
import Util from '../libs/util';
import VueRouter from 'vue-router';
import Cookies from 'js-cookie';
import { routers, otherRouter } from './router';
import store from '../store';

Vue.use(VueRouter);

// 
const RouterConfig = {
    // mode: 'history',
    routes: routers
};

export const router = new VueRouter(RouterConfig);

router.beforeEach((to, from, next) => {
    iView.LoadingBar.start();
    Util.title(to.meta.title);
    if (Cookies.get('locking') === '1' && to.name !== 'locking') { // 
        next({
            replace: true,
            name: 'locking'
        });
    } else if (Cookies.get('locking') === '0' && to.name === 'locking') {
        next(false);
    } else {
        if (!Cookies.get('userInfo') && to.name !== 'login' && to.name !== 'regist' && to.name !== 'regist-result') { // 
            next({
                name: 'login'
            });
        } else if (Cookies.get('userInfo') && to.name === 'login') { // 
            Util.title();
            next({
                name: 'home_index'
            });
        } else {
            const curRouterObj = Util.getRouterObjByName([otherRouter, ...store.state.app.menuList], to.name);
            if (curRouterObj && curRouterObj.access !== undefined && curRouterObj.access !== null) { // 
                let accessCode = Cookies.get('access');
                if (Util.showThisRoute(curRouterObj.access, accessCode)) {
                    Util.toDefaultPage([otherRouter, ...store.state.app.menuList], to.name, router, next); // 
                } else {
                    next({
                        replace: true,
                        name: 'error-403'
                    });
                }
            } else { // , 
                Util.toDefaultPage([...routers], to.name, router, next);
            }
        }
    }
});

router.afterEach((to) => {
    Util.openNewPage(router.app, to.name, to.params, to.query);
    iView.LoadingBar.finish();
    window.scrollTo(0, 0);
});



